import { getActClass } from './utils';

const DEFAULT_DESCRIPTION = '';


export default {
  name: 'PressBindLogin',
  options: {
    virtualHost: true,
  },
  props: {
    title: {
      type: String,
      default: '绑定游戏账号',
    },
    logo: {
      type: String,
      default: 'https://image-1251917893.file.myqcloud.com/live/tg-logo.png',
    },
    name: {
      type: String,
      default: '',
    },
    description: {
      type: String,
      default: DEFAULT_DESCRIPTION,
    },
    showQQ: {
      type: Boolean,
      default: true,
    },
    showWX: {
      type: Boolean,
      default: true,
    },
    agreementName: {
      type: String,
      default: '用户协议',
    },
    showNextAgreement: {
      type: Boolean,
      default: false,
    },
    nextAgreementName: {
      type: String,
      default: '',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'clickQQLogin',
    'clickWXLogin',
    'onCancel',
    'openAgreement',
    'openNextAgreement',
  ],
  data() {
    return {
      isShowPopup: true,
      mIsAgree: false,
      timer: null,
    };
  },
  mounted() {
  },
  methods: {
    clickQQLogin() {
      this.$emit('clickQQLogin');
    },
    clickWXLogin() {
      this.$emit('clickWXLogin');
    },
    clickCancel() {
      this.isShowPopup = false;
      clearTimeout(this.timer);

      this.timer = setTimeout(() => {
        this.$emit('onCancel');
        this.destroy();
      }, 400);
    },
    onClickAgree() {
      this.mIsAgree = !this.mIsAgree;
    },
    openAgreement() {
      this.$emit('openAgreement');
    },
    openNextAgreement() {
      this.$emit('openNextAgreement');
    },
    destroy() {
      setTimeout(() => {
        this.$destroy();

        if (document.body.contains(this.$el)) {
          document.body.removeChild(this.$el);
        }

      }, 100);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};

